* {
  font-family: Georgia, "Times New Roman", Times, serif;
}
body {
  background-color: rgba(8, 144, 223, 0.63);
}

.logo-container {
  background-color: rgb(6, 154, 240);
  border: 3px solid skyblue;
  height: 150px;
  font-size: 30px;
}

.logo-container ul {
  display: flex;
  justify-content: space-around;
  list-style-type: none;
}

.logo-container li {
  align-self: center;
}

.logo-container img {
  height: 100px;
  width: 100px;
}

#hero {
  display: flex;
  background-image: url("assets/chateau.jpg");
  background-color: rgba(8, 144, 223, 0.63);
  padding: 100px;
  align-self: center;

  justify-content: center;
  width: 820px;
  object-fit: cover;
  color: whitesmoke;
}

#royal-family h2 {
  font-size: 50px;
}

.revelation {
  display: flex;
  justify-content: space-around;
}

.revelation img {
  height: 300px;
  width: 300px;
}
.names {
  display: flex;
  justify-content: space-around;
}
.names h3 {
  font-size: 30px;
}
.wealth {
  border-collapse: collapse;
  display: flex;
  justify-content: space-around;
}
.wealth td,
th {
  border: 3px solid goldenrod;
  font-size: 30px;
  text-align: center;
  padding: 8px;
}

.wealth img {
  height: 150px;
  width: 150px;
}
.contact p {
  text-transform: uppercase;
}
.text {
  display: flex;
  flex-direction: column;
  font-size: 30px;
}
#name,
#email,
#message {
  justify-items: center;
  width: 80%;
  padding: 12px 20px;
  margin: 15px 0px;
  box-sizing: border-box;
  font-size: 30px;
}
#name:hover,
#email:hover,
#message:hover {
  background-color: royalblue;
  color: white;
}

#submit {
  background-color: rgb(76, 99, 175);
  color: white;
  padding: 12px 20px;
  border-radius: 4px;
  cursor: pointer;
  float: right;
  font-size: 25px;
}

#submit:hover {
  background-color: rgb(27, 49, 121);
}

.btnPopup {
  background-color: orangered;
  padding: 1em 2em;
  cursor: pointer;
}

.btnPopup:hover {
  background-color: rgb(192, 192, 192);
}

.overlay {
  position: fixed;
  left: 0px;
  top: 0px;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  z-index: 1;
  display: none;
}

.popup {
  margin: 10% auto;
  width: 70%;
  background-color: rgb(243, 243, 243);
  padding: 1em;
  box-shadow: 0 15px 20px rgba(0, 0, 0, 0.3);
  border-radius: 5px;
}

.btnClose {
  float: right;
  font-size: 16pt;
  cursor: pointer;
  color: rgb(26, 26, 26);
}
.erreur {
  color: red;
  font: bolder;
}
